import Vue from 'vue'
import Router from 'vue-router'

import frameRoutes from './routes/frames'
import route404 from './routes/404'

Vue.use(Router)

//  404 
const globalRoute404 = {
  ...route404,
  path: '/404'
}

// Vue Router 
const $router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/default/page/1'
    },

    // 
    ...frameRoutes,

    //  404
    globalRoute404,

    //  404
    {
      path: '*',
      redirect(to) {
        const match = /^(\/[^/]+\/)/.exec(to.path)

        if (match) {
          const base = match[1]
          const matchParent = $router.options.routes.find(
            item => item.path === base
          )

          //  404
          if (matchParent) return base + '404'
        }

        //  404
        return '/404'
      }
    }
  ]
})

export default $router
