// 
import { emptyObj } from '../../util'

// 
import contextmenu from './contextmenu'
import i18n from './i18n'
import iframe from './iframe'
import operate from './operate'
import pageLeave from './pageLeave'
import rule from './rule'
import scroll from './scroll'
import restore from './restore'

// 
import RouterAlive from '../RouterAlive'
import TabItem from './TabItem.vue'

// RouterTab 
const RouterTab = {
  name: 'RouterTab',
  mixins: [
    contextmenu,
    i18n,
    iframe,
    operate,
    pageLeave,
    rule,
    scroll,
    restore
  ],
  components: { RouterAlive, TabItem },

  // 
  provide() {
    return { Tab: this }
  },

  props: {
    // 
    tabs: {
      type: Array,
      default: () => []
    },

    // 
    restore: {
      type: [Boolean, String],
      default: false
    },

    //  restoreKey 
    restoreWatch: {
      type: Boolean,
      default: false
    },

    // 
    keepLastTab: {
      type: Boolean,
      default: true
    },

    // 
    defaultPage: [String, Object],

    /**
     * 
     * -  'zh-CN' ()  'en'
     * - 
     */
    language: {
      type: [String, Object],
      default: 'zh-CN'
    },

    //  i18n (key, [args])
    i18n: Function,

    // router-view
    routerView: Object,

    // 
    tabTransition: {
      type: [String, Object],
      default: 'router-tab-zoom'
    },

    // 
    pageTransition: {
      type: [String, Object],
      default: () => ({
        name: 'router-tab-swap',
        mode: 'out-in'
      })
    }
  },

  data() {
    return {
      loading: false, //  loading
      items: [], // 
      activeTabId: null, //  id
      isViewAlive: true,
      isMounted: false, // 
      isMobile: false // 
    }
  },

  computed: {
    // 
    defaultPath() {
      return this.defaultPage || this.getBasePath() || '/'
    },

    // routerAlive
    $alive() {
      return this.isMounted ? this.$refs.routerAlive : null
    },

    // 
    activeTab() {
      return this.items.find(item => item.id === this.activeTabId)
    }
  },

  watch: {
    // 
    $route($route) {
      this.loading = false
      this.updateActiveTab()
      this.fixCommentPage()
    }
  },

  created() {
    // 
    RouterTab.Vue.prototype.$routerTab = this

    this.initTabs()
    this.updateActiveTab()
  },

  mounted() {
    this.isMounted = true

    // 
    this.isMobile = /mobile/i.test(navigator.userAgent)
  },

  destroyed() {
    const proto = RouterTab.Vue.prototype
    // 
    if (proto.$routerTab === this) {
      proto.$routerTab = null
    }
  },

  methods: {
    // 
    initTabs() {
      if (this.restoreTabs()) return

      this.presetTabs()
    },

    // 
    presetTabs(tabs = this.tabs) {
      let ids = {}

      this.items = tabs
        .map((item, index) => {
          item = typeof item === 'string' ? { to: item } : item || emptyObj

          let route = item.to && this.$router.match(item.to)

          if (route) {
            let tab = this.getRouteTab(route)
            let id = tab.id

            //  id 
            if (!ids[id]) {
              // id 
              delete item.id

              //  tab 
              return (ids[id] = Object.assign(tab, item))
            }
          }
        })
        .filter(item => !!item)
    },

    // 
    updateActiveTab() {
      this.activeTabId = this.getAliveId()
    },

    //  tab 
    updateTab(key, { route, tab }) {
      let { items } = this
      let matchIdx = items.findIndex(({ id }) => id === key)

      let item = Object.assign(this.getRouteTab(route), tab)

      if (matchIdx > -1) {
        let matchTab = items[matchIdx]
        item.closable = matchTab.closable !== false
        this.$set(items, matchIdx, item)
      } else {
        items.push(item)
      }
    },

    //  aliveId
    getIdByPath(path, match = true) {
      if (!path) return

      // 
      if (match) {
        let route = this.$router.match(path, this.$router.currentRoute)
        let matchPath = this.getPathWithoutHash(route)
        let matchTab = this.items.find(
          ({ to }) => to.split('#')[0] === matchPath
        )

        if (matchTab) {
          return matchTab.id
        }
      } else {
        return this.getAliveId(path)
      }
    },

    //  route  tab 
    getRouteTab(route, matchRoutes = this.matchRoutes(route)) {
      const tab = Object.create(matchRoutes.pageRoute.meta)

      return Object.assign(tab, {
        id: this.getAliveId(route),
        to: route.fullPath
      })
    },

    // 
    getTransOpt(trans) {
      return typeof trans === 'string' ? { name: trans } : trans
    },

    // 
    async reloadView(ignoreTransition = false) {
      this.isViewAlive = false

      //  isViewAlive  true
      //  ignoreTransition  true 
      if (ignoreTransition) {
        await this.$nextTick()
        this.isViewAlive = true
      }
    },

    // 
    onTabTransitionEnd() {
      this.adjust()
    },

    // 
    onPageTransitionEnd() {
      if (!this.isViewAlive) this.isViewAlive = true
    },

    //  mount comment 
    fixCommentPage() {
      if (this.$alive.$el.nodeType === 8) {
        this.reloadView(true)
      }
    }
  }
}

export default RouterTab
